.footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 15px;
  background-color: black;
}

.footer,
.footer * {
  font-size: 10px;
  color: #999;
}

.footer-menu-links:hover {
  color: white;
}

.footer-menu-items {
  display: inline-block;
}

.footer-menu-links {
  display: inline-block;
  padding: 7px;
}